import React, {useState, useEffect} from 'react';
import { Typography, Box, Grid, Container, Paper } from '@mui/material'
import MedicationIcon from '@mui/icons-material/Medication';
import MasksIcon from '@mui/icons-material/Masks';
import SpaIcon from '@mui/icons-material/Spa';
import Groups2Icon from '@mui/icons-material/Groups2';
import banner from "../../img/banner.png"

const Introduction= () => {

    const [isMobile, setIsMobile] = useState(false)
 
    //choose the screen size 
    const handleResize = () => {
        if (window.innerWidth < 720) {
            setIsMobile(true)
        } else {
            setIsMobile(false)
        }
    }

    useEffect(() => {
        window.addEventListener("resize", handleResize)
      })
    return (
        <Box>
            <Container>
                <img src={banner} className="banner"/>
                {!isMobile ? <Box sx={{display: "block", margin: "0 auto", padding: "10px", backgroundColor: "#0CF2B4", width: "600px", borderRadius: "50px", marginBottom: "15px"}}>
                        <p className='title'> KHM BNH TN <span className='special-title'>TM</span>, NNG  <span className='special-title'>TM</span> SC KHE </p> 
                    </Box> : <Box sx={{display: "block", margin: "0 auto", padding: "10px", backgroundColor: "#0CF2B4", maxWidth: "300px", borderRadius: "50px", marginBottom: "15px"}}>
                        <p className='title-2'> KHM BNH TN <span className='special-title-2'>TM</span>, NNG  <span className='special-title-2'>TM</span> SC KHE </p> 
                    </Box>  }
                <Grid container>
                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6} sx={{display: "flex", alignItems: "center"}}>  
                    </Grid>
                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                        
                        <Typography variant="body1" sx={{fontWeight: 400, marginBottom: "15px"}}>
                            HEALTECH l app cung cp cho ngi dng thng tin lin quan v cc loi thuc cng nh tng tc t vn, chm sc cho ngi bnh t xa thng qua cc tnh nng
                        </Typography> 
                        <Grid container>
                            <Grid item xs={6} sx={{padding: "20px"}}>
                                <Paper elevation={2} sx={{display: "flex", flexDirection: "column", alignItems: "center", height: "130px"}}>
                                    <MedicationIcon sx={{fontSize: "70px", marginTop: "15px"}}/>
                                    <Typography variant="body2" sx={{fontWeight: 700, fontStyle: "italic", textAlign: "center"}}>Tra cu thng tin, t v mua thuc</Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={6} sx={{padding: "20px"}}>
                                <Paper elevation={2} sx={{display: "flex", flexDirection: "column", alignItems: "center", height: "130px"}}>
                                    <MasksIcon sx={{fontSize: "70px", marginTop: "15px"}}/>
                                    <Typography variant="body2" sx={{fontWeight: 700, fontStyle: "italic", textAlign: "center"}}>T vn bc s</Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={6} sx={{padding: "20px"}}>
                                <Paper elevation={2} sx={{display: "flex", flexDirection: "column", alignItems: "center", height: "130px"}}>
                                    <SpaIcon sx={{fontSize: "70px", marginTop: "15px"}}/>
                                    <Typography variant="body2" sx={{fontWeight: 700, fontStyle: "italic", textAlign: "center"}}>Y hc c truyn</Typography>
                                </Paper>
                            </Grid>
                            <Grid item xs={6} sx={{padding: "20px"}}>
                                <Paper elevation={2} sx={{display: "flex", flexDirection: "column", alignItems: "center", height: "130px"}}>
                                    <Groups2Icon sx={{fontSize: "70px", marginTop: "15px"}}/>
                                    <Typography variant="body2" sx={{fontWeight: 700, fontStyle: "italic", textAlign: "center"}}>Xy dng cng ng</Typography>
                                </Paper>
                            </Grid>
                        </Grid>
                </Grid>
            </Grid>  
            </Container>
            
            
        </Box>
        
    )
}

export default Introduction